.products {
    height:300px;
    background-color: rgb(244, 244, 244);
    color:rgb(0, 0, 0);
    text-shadow: 2px 2px 1px rgb(135, 135, 135) ;
    display:flex;
    justify-content: center;
    align-items: center;
}

.products img {
    max-height:30vh;

    justify-content: center;
    filter: drop-shadow(2px 6px 3px #474747);
}

.products div {
   
    

   flex-direction: column;
   justify-content: center;
   align-items: center;
    display:flex;
}

.products button  {
    width:20vw;
    height:5vh;
    font-size: medium;
    color:black;
   
}

@media only screen and (max-width:1000px) {
    .products {
       padding:0vw 8vw 0 7vw;
        font-size:3vw;
        
    }
    .products div {
        gap:0.5vw;
    }
  .products img {
  height:50vw;
  }
    .products button {
        width:30vw;
    }
    #goback {
        font-size:4.5vw;
    }

}