.shop {
    display:flex;
    background-color: rgba(245, 245, 245, 0.484);
   
    justify-content: center;
}

.left-side {
    display:flex;
    flex-direction: column;
    align-items: center;
    margin:20px 10vw 10px 10px;
  
}
.left-side p {

 
 
   text-shadow: 1px 1px rgb(0, 0, 0);
   -webkit-text-stroke: 0.5px  black;
   color:white;
   background-color: rgb(255, 237, 220);
}

.left-side .chosenCategory {
   
font-size:4vw;
   justify-content: center;
   display:flex;
  margin-left:3rem;
   margin-bottom:3vw;
}

.left-side .category {
    display:flex;
    flex-direction: column;
    align-items: center;
    list-style-type:none;
    color:black;
    font-size: 2.5vw;
    text-decoration: none;
   transition: color 1s linear 2s;

  
}
.category:hover {
color:rgba(218, 177, 130, 0.462);
}
.right-side {
    display:grid;
    grid-template-columns: 1fr 1fr 1fr ;
    gap:1vw;
   padding-top:2vw;
  background-color: rgba(236, 236, 236, 0.044);
    align-items: center;

  
}
.right-side .image {
    height:20vw;
    width:20vw;
    
    background-color: rgba(171, 171, 171, 0.096);
   ;
    display:flex;
   
    justify-content: center;
   border:2px groove rgba(231, 231, 231, 0.042);
}
.right-side .productinfo {
   font-size: 1vw;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-decoration: none;
}


.left-side li {




    color: black;
}


.image img {
    filter: drop-shadow(10px 2px 1px #e2e2e2);
    height:18vw;
}

@media(max-width:900px) {
    .right-side {
        grid-template-columns: 1fr 1fr;
    }
}

@media only screen and (max-width:1000px) {
    
    .shop {
        margin-left:0;
        flex-direction:column;
    }
    .left-side{
        flex-direction: column;
        margin:0;
    }
    .left-side .chosenCategory {
 font-size: 8vw;
margin-right:5vw;
 
    }

    .left-side .categories {
        display:flex;
        justify-content: center;
      flex-direction: row;
      gap:6vw;
  width:100%;

 
     
     


      
    }
    .left-side .category {
        font-size:6vw;
        color:rgb(100, 100, 100)
    }

.right-side .image {
 height:50vw;    
width:50vw;
}
    .right-side .image  img{
        height:50vw;
    }

    .right-side .productinfo {
        font-size:3vw;
    }
}