*{
    margin:0;
}
.link {
   
    background: -webkit-linear-gradient(#ffd000cd, rgb(169, 169, 169));
    -webkit-background-clip: text;
    background-clip: none;
    -webkit-text-fill-color: transparent;

    text-shadow: 2px 2px 8px rgba(255, 255, 255, 0.979);
    -webkit-text-stroke: 0.2px rgba(0, 0, 0, 0.912);
    text-decoration: none;
}
.navbar {

width:100%;
justify-content: space-between;
background-color: transparent;
display:flex;

align-items: center;


}
.logo {

    font-size:2rem;
    color: rgb(163, 0, 0);
  
}
.menu h1 {
font-size:1.6rem
}
.shoppingCart {
  
    

  display:flex;
    position:relative;
font-size:1.8rem
     
}
.shoppingCart .cart {
    color:black
    
}
.shoppingCart p {

    position: absolute;
    width:1rem;
    height:1rem;
    font-size: 2vh;
    bottom: 6px;
    right: -6px;

    background-color: #ffd000;
    text-shadow:0px 2px black;
    display:flex;
    justify-content: center;
    align-items: center;
    border-radius: 100px;
 visibility: hidden;
}
.menu {

    display:flex;
    justify-content: center;
    align-items: center;
    gap:6vw;
    font-size: 1.5rem;
    text-decoration: none;
   padding:0px 6vw 0px 25px;
    color:rgb(255, 255, 255);
    
}

   


@media (max-width:800px) {
    .navbar {
        flex-direction: column;
    }
    .menu {
        margin-bottom:5px;
    }
    .logo {
        font-size: 3rem;
    
    }
}

@media only screen and (max-width: 600px) {
    .navbar {
        padding-top:5vw;
    }
    .logo {
        font-size:9vw;
    }
    .shoppingCart .svg {
        
        font-size:6vw;
    }
   
    .shoppingCart p {
 right:-2vw;
 top:3vw;
        font-size:4vw;
        height:4vw;
        width:4vw;
        border:0.01rem solid rgba(0, 0, 0, 0.259);
        background-color: #ffca4e;
    }
    .menu h1 {
        font-size:7vw;
    }
}